.aboutus-model{
    min-height: 100vh;
    width: 100%;
    height: 100%;
    padding: 6vh 4.1vw;
    display: flex;
    flex-direction: column;
    gap: 4vh;
}

.aboutus-model > h1{

    text-align: center;
    font-family: "Montserrat", sans-serif;
    font-size: 5vh;
    color: #03AA6B;
}


.aboutus-model > p{
    line-height: 1.7rem;
    letter-spacing: 1px;
}

.about-model-div{
    
    display: flex;
    flex-wrap: wrap;
    width: 100%;
    height: 100%;
    gap: 3vh;
}

.about-model-each-div{
    position: relative;
    width: 49%;

    height: 100%;
    border-radius: .7rem;
    overflow: hidden; /* Ensure pseudo-element stays within the div */
    display: flex;
    flex-direction: column;
    gap: 3rem;
  
}


.about-model-each-div:nth-child(2)::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-image: url('../../assests/newpic/engineerpng.png'); /* Update with the correct path */
    background-size: contain;
    background-position: center;
    background-repeat: no-repeat;
    opacity: 0.4; /* Adjust the opacity to make it look like a watermark */
    z-index: 0; /* Place the pseudo-element behind the content */
}

.about-model-each-div:nth-child(3)::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-image: url('../../assests/newpic/surveor.png'); /* Update with the correct path */
    background-size: contain;
    background-position: center;
    background-repeat: no-repeat;
    opacity: 0.4; /* Adjust the opacity to make it look like a watermark */
    z-index: 0; /* Place the pseudo-element behind the content */
}

.about-model-each-div:nth-child(6)::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-image: url('../../assests/newpic/consultant.png'); /* Update with the correct path */
    background-size: contain;
    background-position: center;
    background-repeat: no-repeat;
    opacity: 0.4; /* Adjust the opacity to make it look like a watermark */
    z-index: 0; /* Place the pseudo-element behind the content */
}
.about-model-each-div h3, .about-model-each-div h1, .about-icons {
    position: relative; /* Ensure text and icons are above the pseudo-element */
    z-index: 1;
}

.about-model-each-div p{
    position: relative; /* Ensure text and icons are above the pseudo-element */
    z-index: 1;
}
.about-camera-watermark{
    position: absolute;
    filter: blur(10%);
}

.about-model-each-div p{
    letter-spacing: .3vw;
    font-size: 2.5vh;
    color: black;
    font-weight: 900;
    line-height: 4vh;
}

.about-model-each-div h3{
    letter-spacing: .3vw;
    font-size: 2.5vh;
    font-weight: 900;
    line-height: 4vh;
}

.about-model-each-div h1{
    letter-spacing: .3vw;
    font-size: 5vh;
   
    text-align: center;
    font-weight: 900;
    
}


.about-model-each-div:first-child{
   
    justify-content: flex-end;

   
}

.about-pic-info{
    width: fit-content;
    letter-spacing: .15vw;
    padding: 1vh 1vw;
    border-radius: 1rem;
    background-color: rgba(0, 0, 0, 0.548);
}

.about-model-each-div:nth-child(4){

    justify-content: flex-end;
   
}


.about-model-each-div span{
    color: rgba(165, 27, 27, 0.856);
}

.about-icons{
    display: flex;
    gap: 1.5vw;
}

.about-icon-model{
    color: black;
    font-size: 5vh;
}



