figure.snip1113 {
    font-family: 'Raleway', Arial, sans-serif;
    position: relative;
    overflow: hidden;
  
    
    width: 100%;
    background: hsl(0, 1%, 86%);
  
    text-align: center;
    box-shadow: 0 0 5px rgba(0, 0, 0, 0.15);
  }
  
  figure.snip1113 * {
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
    -webkit-transition: all 0.3s ease-in-out;
    transition: all 0.3s ease-in-out;
  }
  
  figure.snip1113 img {
    max-width: 65%;
    margin: 6vh auto;
    display: block;
    position: relative;
    border: .7vh solid #666666;
    padding: 15px 15px 85px 15px;
    -webkit-transform: translate(0, 0);
    transform: translate(0, 0);
  }
  
  figure.snip1113 figcaption {
    position: absolute;
    height: 20vh;
    bottom: 0px;
    left: 0;
    right: 0;
    display: block;
  }
  
  figure.snip1113 h3 {
    background-color: #ffffff;
    color: #000000;
    font-size: 3.5vh;
    width: 100%;
    padding: 5px 12px;
    margin: 0;
    text-transform: uppercase;
    font-weight: 400;
  }
  
  figure.snip1113 h3 span {
    font-weight: 800;
  }
  
  figure.snip1113 h4 {
    margin: 0;
    text-transform: uppercase;
    letter-spacing: 1px;
    opacity: 1;
    background-color: #3a3939;
    font-size: 2.5vh;
  }
  
  figure.snip1113.blue img {
    border-color: #2472a4;
  }
  
  figure.snip1113.blue h3,
  figure.snip1113.blue h4 {
    color: #2472a4;
  }
  
  figure.snip1113.red img {
    border-color: #ab3326;
  }
  
  figure.snip1113.red h3 {
    color: #ab3326;
  }
  
  
  figure.snip1113.red h4 {
      color: white;
    }
  figure.snip1113.yellow img {
    border-color: #e08e0b;
  }
  
  figure.snip1113.yellow h3,
  figure.snip1113.yellow h4 {
    color: #e08e0b;
  }
  
  figure.snip1113.green img {
    border-color: #229955;
  }
  
  figure.snip1113.green h3,
  figure.snip1113.green h4 {
    color: #229955;
  }
  
  figure.snip1113.orange img {
    border-color: #d67118;
  }
  
  figure.snip1113.orange h3,
  figure.snip1113.orange h4 {
    color: #d67118;
  }
  
  figure.snip1113.navy img {
    border-color: #2b3c4e;
  }
  
  figure.snip1113.navy h3,
  figure.snip1113.navy h4 {
    color: #2b3c4e;
  }
  
  
  /* Demo purposes only */
  @media (max-width: 600px) {

    .aboutus-model{
      min-height: 100vh;
      width: 100%;
      height: 100%;
      padding: 16vh 4.1vw;
      display: flex;
      flex-direction: column;
      gap: 4vh;
  }
  
    .about-model-div{
      padding: 0 5vw;
      display: flex;
      flex-wrap: wrap;
      width: 100%;
      height: 100%;
      gap: 3rem;
  }
  
  .about-model-each-div{
      position: relative;
      width: 100%;
      padding: 2.2vh 3vw;
      height: 24%;
      border-radius: .7rem;
     
      display: flex;
      flex-direction: column;
      gap: 2rem;
  }
  
  .about-model-each-div h3{
      letter-spacing: 3px;
      font-size: .8rem;
      font-weight: 900;
      line-height: 1.5rem;
  }

  .about-model-each-div p{
      letter-spacing: 3px;
      font-size: .8rem;
      font-weight: 900;
      line-height: 1.5rem;
  }
  
  
  .about-model-each-div h1{
      letter-spacing: 3px;
      font-size: 2rem;

      text-align: center;
      font-weight: 900;
      line-height: 1.5rem;
  }
  
  .about-icons{
    
      gap: 1rem;
  }
  
  .about-icons img{
      width: 2rem;
      height: 2rem;
  }

  .about-model-each-div:nth-child(3){
      
      order: 4; /* Swap the order */
  }
  
  .about-model-each-div:nth-child(4){
      order: 3; /* Swap the order */
  }
  }