@import url(https://fonts.googleapis.com/css?family=Montserrat:400,700);
.contact-model{
    width: 100%;
    height: 100%;
    min-height: 100vh;
    padding: 6vh 4.1vw;
    
}

.contact-map{
    margin-top: 4vh;
}


.contact-model > h1{
    text-align: center;
    font-family: "Montserrat", sans-serif;
    font-size: 5vh;
    color: #03AA6B;
}

.underline {
    border-bottom: solid 2px #474544;
    margin: -0.512em auto;
    width: 80px;
  }

.contact-model form{
    display: flex;
    background-color: #f2f2f2;
    flex-direction: column;
    gap: 3vh;
    margin-top: 10vh;
    border: 4px solid black;
    text-align: center;
    padding: 1rem;
}

.contact-model form h2{
    font-size: 6vh;
    color: #03AA6B;
    letter-spacing: 2px ;
    text-transform: uppercase;
}


.icon_wrapper{
    width: 5vw;
    height: 10vh;
    margin: auto;
}

.contact-model input{
    padding: .5rem 1rem;
    border: none;
    background-color: transparent;
    border-bottom: 1px solid black;
    outline: none;
    letter-spacing: 1px;
}

.contact-model button{
    color: black;
    padding: .5rem 1rem;
    background-color: #03AA6B;
    outline: none;
    border: none;
    cursor: pointer;
    width: 30%;
}

.contact-model textarea{
    resize: none;
    background-color: transparent;
    height: 30vh;
    width: 100%;
    padding: .5rem 1rem;
}

@media (max-width: 600px) {
   
    
    .contact-map{
        margin-top: 4vh;
    }
    

    
    .underline {
        border-bottom: solid 2px #474544;
        margin: -0.512em auto;
        width: 80px;
      }
    
    .contact-model form{
        display: flex;
        background-color: #f2f2f2;
        flex-direction: column;
        gap: 3vh;
        margin-top: 10vh;
        border: 4px solid black;
        text-align: center;
        padding: 1rem;
    }
    
    .contact-model form h2{
        font-size: 1.8rem;
        color: #03AA6B;
        letter-spacing: 2px ;
        text-transform: uppercase;
    }
    
    
    .icon_wrapper{
        width: 3rem;
        height:  3rem;
        margin: auto;
    }
    
    .contact-model input{
        padding: .5rem 1rem;
        border: none;
        background-color: transparent;
        border-bottom: 1px solid black;
        outline: none;
        letter-spacing: 1px;
    }
    
    .contact-model button{
        color: black;
        padding: .5rem 1rem;
        background-color: #03AA6B;
        outline: none;
        border: none;
        cursor: pointer;
        width: 40%;
    }
    
    .contact-model textarea{
        resize: none;
        background-color: transparent;
        height: 30vh;
        width: 100%;
    }
    
}

